<template>
  <!-- <div class="table-responsive"> -->
  <div class="row">
    <div
      class="col-xl-12 col-lg-12 ml-0 pl-0 mb-0"
      v-for="(row, index) in predictions"
      :key="index"
    >
      <stats-card class="mb-xl-0 mb-0">
        <!-- title="ALL LEAGUES"
            type="gradient-red"
            sub-title="350"
        icon="fa fa-trophy"-->
        <div class="row">
          <div class="col-8 col-sm-8 col-md-10">
            <h5 class="card-title text-uppercase text-muted mt-2 mb-0">
              <i :class="'ni ni-trophy mr-2'"></i>
              {{row.title}}
            </h5>
            <small class="font-weight-bold mb-0">{{row.website_url}}</small>
          </div>
          <div class="col-4 col-sm-4 col-md-2">
            <slot name="icon">
              <div
                class="icon icon-shape text-white rounded-circle shadow"
                :class="row.outcome == 'accurate' ? 'bg-gradient-success' : 'bg-gradient-red'"
              >
                <base-button
                  size="sm"
                  @click="$emit('allMatches')"
                  :class="row.outcome == 'accurate' ? 'bg-gradient-success' : 'bg-gradient-red'"
                  style="border: none;"
                >{{row.prediction}}</base-button>
              </div>
            </slot>
          </div>

          <div class="col-12 col-sm-12 col-md-12">
            <small class="mr-2">
              Accuracy:
              <b>{{row.accuracy}}%</b>
            </small>
            <small class="mr-2">
              Matches:
              <b>{{row.predictions}}</b>
            </small>
            <base-progress
              :type="row.accuracy <= 30 ? 'gradient-danger': 'gradient-success'"
              class="pt-0"
              :show-percentage="false"
              :value="row.accuracy"
            />
            <!-- <span class="text-nowrap">Since Yesterday</span> -->
          </div>
        </div>
      </stats-card>
    </div>
    <!-- </div> -->
    <!-- <base-table thead-classes="thead-light" :data="predictions">
      <template slot="columns">
        <th>Prediction Website</th>
        <th>Prediction</th>
        <th>Accuracy</th>
        <th>Matches</th>
      </template>

      <template slot-scope="{row}">
        <th scope="row">{{row.website_url}}</th>
        <td>{{row.prediction}}</td>
        <td>
          <div class="d-flex align-items-center">
            <span class="mr-2">{{row.accuracy}}%</span>
            <base-progress
              :type="row.accuracy <= 30 ? 'gradient-danger': 'gradient-success'"
              class="pt-0"
              :show-percentage="false"
              :value="row.accuracy"
            />
          </div>
        </td>
        <td>{{row.predictions}}</td>
      </template>
    </base-table>-->
  </div>
</template>
<script>
export default {
  name: "social-traffic-table",
  props: ["predictions"],
  data() {
    return {
      tableData: [
        {
          name: "Facebook",
          visitors: "1,480",
          progress: 60,
          progressType: "gradient-danger"
        },
        {
          name: "LinkedIn",
          visitors: "5,480",
          progress: 70,
          progressType: "gradient-success"
        },
        {
          name: "Google",
          visitors: "4,807",
          progress: 80,
          progressType: "gradient-primary"
        },
        {
          name: "Instagram",
          visitors: "3,678",
          progress: 75,
          progressType: "gradient-info"
        },
        {
          name: "Twitter",
          visitors: "2,645",
          progress: 30,
          progressType: "gradient-warning"
        }
      ]
    };
  }
};
</script>
