    VuetablePagination,
    VuetablePaginationInfo
  },
  mounted() {
    this.staff = "";
  },
  watch: {
    selectedDate: function() {
      //   console.log("Date has changes");
      this.$refs.vuetable.refresh();
      this.$refs.vuetable.visibleDetailRows = [];
    }
  },
  computed: {},
