          ascendingIcon: "glyphicon glyphicon-chevron-up",
          descendingIcon: "glyphicon glyphicon-chevron-down"
        },
        pagination: {
          wrapperClass: "pagination",
          activeClass: "active",
          disabledClass: "disabled",
          pageClass: "page",
          linkClass: "link",
          icons: {
            first: "",
            prev: "",
            next: "",
            last: ""
          }
        },
        icons: {
          first: "glyphicon glyphicon-step-backward",
          prev: "glyphicon glyphicon-chevron-left",
          next: "glyphicon glyphicon-chevron-right",
          last: "glyphicon glyphicon-step-forward"
        }
      },
      sortOrder: [{ field: "email", sortField: "email", direction: "asc" }],
      moreParams: {}
    };
  },
  methods: {
    allcap(value) {
      return value.toUpperCase();
    },
    statusLabel(value) {
      return value === "0"
        ? '<span class="label label-success"><i class="glyphicon glyphicon-star"></i>active    <mu-badge content="old" color="primary"></mu-badge></span>'
        : '<span class="label label-danger"><i class="glyphicon glyphicon-heart"></i> inactive</span>';
    },
    roleLabel(value) {
      switch (value) {
        case "1":
          return "Manager";
        case "0":
          return "Administrator";
        default:
          return "Normal user";
      }
    },
    formatNumber(value) {
      return accounting.formatNumber(value, 2);
    },
    formatDate(value, fmt = "D MMM YYYY") {
      return value == null ? "" : moment(value, "YYYY-MM-DD").format(fmt);
    },
    onPaginationData(paginationData) {
      this.$refs.pagination.setPaginationData(paginationData);
      this.$refs.paginationInfo.setPaginationData(paginationData);
    },
    onChangePage(page) {
      this.$refs.vuetable.changePage(page);
    },
    onCellClicked(data) {
      this.$refs.vuetable.toggleDetailRow(data.id);
    },

    onLoading() {
      this.loading = true;
      // console.log("loading.../ show your spinner here");
    },
    async onLoaded() {
      this.loading = false;
      this.$refs.vuetable.visibleDetailRows = await this.$refs.vuetable.tableData.map(
        function(item) {
          return item.id;
        }
      );
      // console.log("loaded! .. hide your spinner here");
    },

    refreshVueTable() {
      Vue.nextTick(() => this.$refs.vuetable.refresh());
    }
  },
  events: {
    "filter-set"(filterText) {
      this.moreParams = {
        filter: filterText
      };
      Vue.nextTick(() => this.$refs.vuetable.refresh());
    },
    "filter-reset"() {
      this.moreParams = {};
      Vue.nextTick(() => this.$refs.vuetable.refresh());
    },
    "refresh-table"() {
      Vue.nextTick(() => this.$refs.vuetable.refresh());
    }
  }
};
</script>
<style>
