                  <div
                    class="icon icon-shape text-white rounded-circle shadow"
                    :class="[`bg-gradient-danger`]"
                  >
                    <!-- <i :class="'ni ni-chart-bar-32'"></i> -->
                    <base-button
                      size="sm"
                      @click="$emit('allMatches')"
                      class="bg-gradient-danger"
                      style="border: none;"
                    >{{row.abbrev}}</base-button>
                  </div>
                  <base-button
                    size="sm"
                    @click="$emit('allMatches')"
                    class="bg-gradient-success ml-4"
                    style="border: none;"
                  >
                    <i :class="'fa fa-clock'"></i>
                    {{row.status == "" ? "pending" : (row.status == "Finished" || row.status == "Postponed" || row.status == "Awaitingupdates" || row.status == "Half Time" || row.status == "FRO" ) ? row.status : row.status + " Mins" }}
                  </base-button>
                </slot>
