    },
    setCalDate(val) {
      // console.log("I AM HERE");
      // console.log(val.toISOString().slice(0, 10));
      this.$router.push(val.toISOString().slice(0, 10));
    },
    changeDate(val) {
      switch (val) {
        case "tomorrow":
          this.selectedDate = new Date();
          this.selectedDate.setDate(this.selectedDate.getDate() + 1);
          this.selectedDate = new Date(this.selectedDate)
            .toISOString()
            .slice(0, 10);
          break;
        case "today":
          this.selectedDate = new Date();
          this.selectedDate.setDate(this.selectedDate.getDate());
          this.selectedDate = new Date(this.selectedDate)
            .toISOString()
            .slice(0, 10);
          break;
        case "yesterday":
          this.selectedDate = new Date();
          this.selectedDate.setDate(this.selectedDate.getDate() - 1);
          this.selectedDate = new Date(this.selectedDate)
            .toISOString()
            .slice(0, 10);
          break;
      }

      this.$router.push(this.selectedDate);
    }
